import { Component, OnInit } from '@angular/core';
import { SmsService } from "../../SMS.service";
import { Constant } from "../../constant";
import { Router, ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-dashboard-two',
  templateUrl: './dashboard-two.component.html',
  styleUrls: ['./dashboard-two.component.css']
})
export class DashboardTwoComponent implements OnInit {

  constructor(private service: SmsService, private router: Router) { }

  reportResponse: Array<any> = [];
  loader: boolean = false;
  ngOnInit() {

    this.service.get("getAllDashboardReportTask").subscribe(response => {
      this.reportResponse = response;
    });
  }

  viewReport(taskId: any, taskName: any) {
    console.log(taskId);
    localStorage.removeItem("taskId");
    localStorage.removeItem("taskName");
    localStorage.setItem("taskId", taskId);
    localStorage.setItem("taskName", taskName);
    this.router.navigate(['home/dashboardOne']);
  }

  id: any;
  setId(id) {
    this.id = id;
  }

  delete() {
    console.log(this.id);
    this.service.delete("tasks/" + this.id).subscribe(response => {
      if (response) {
        this.ngOnInit();
      }
    })
  }
}



