  resetList() {
    this.color = [];
    this.productBuy = [];
    this.productNotBuying = [];
    this.workingStyle = [];
    this.email = [];
    this.compatibility = [];
    this.EmploymentAffinity = [];
    this.EmploymentNonAffinity = [];
    this.Dominiant = [];
    this.buyingStyle = [];
    this.age = [];
    this.gender = [];

  }

  resetForm() {
    this.resetList();
    this.listdata = false;
    this.searchResponse = [];
    this.selectedColos = [];
    this.selectedProductsBuy = [];
    this.selectedPNB = [];
    this.selectedWorkingStyle = [];
    this.selectedEmail = [];
    this.selectedcompatibility = [];
    this.selectedEmploymentAffinity = [];
    this.selectedEmploymentNonAffinity = [];
    this.selectedDominiant = [];
    this.selectedBuyingStyle = [];
    this.selectedAge = [];
    this.selectedGender = [];
    this.modal = new searchModel();
  }

  listdata: boolean = false;
  Generate() {
    this.loader = true;
    this.resetList();
    this.filterValue();
    this.searchResponse = [];
    this.modal = new searchModel();
    this.modal.taskId = this.taskId;
    if (this.color.length > 0) {
      this.modal.attributes.push({ "attr": "top_color_matches", "value": this.color.toString() });
    }
    if (this.productNotBuying.length > 0) {
      this.modal.attributes.push({ "attr": "things_that_they_would_not_buy", "value": this.productNotBuying.toString() });
    }
    if (this.productBuy.length > 0) {
      this.modal.attributes.push({ "attr": "things_that_they_would_buy", "value": this.productBuy.toString() });
    }

    if (this.workingStyle.length > 0) {
      this.modal.attributes.push({ "attr": "personality_style", "value": this.workingStyle.toString() });
    }


    if (this.email.length > 0) {
      this.modal.attributes.push({ "attr": "personalities_that_match_well", "value": this.email.toString() });
    }

    if (this.compatibility.length > 0) {
      this.modal.attributes.push({ "attr": "multiple_intelligence", "value": this.compatibility.toString() });
    }


    if (this.EmploymentAffinity.length > 0) {
      this.modal.attributes.push({ "attr": "employment_that_fits_well", "value": this.EmploymentAffinity.toString() });
    }


    if (this.EmploymentNonAffinity.length > 0) {
      this.modal.attributes.push({ "attr": "employment_that_doesnt_fit_well", "value": this.EmploymentNonAffinity.toString() });
    }


    if (this.Dominiant.length > 0) {
      this.modal.attributes.push({ "attr": "dominant_attr", "value": this.Dominiant.toString() });
    }


    if (this.buyingStyle.length > 0) {
      this.modal.attributes.push({ "attr": "buyingPattern", "value": this.buyingStyle.toString() });
    }

    if (this.age.length > 0) {
      this.modal.attributes.push({ "attr": "age", "value": this.age.toString() });
    }


    if (this.gender.length > 0) {
      if (this.gender.length == 2) {
        this.gender = [];
      } else {
        this.modal.attributes.push({ "attr": "gender", "value": this.gender.toString() });
      }
    }
    this.service.post("getFilteredEmails", this.modal).subscribe(response => {
      setTimeout(() => {
        this.searchResponse = response["emailList"];
        this.loader = false;
        if (this.searchResponse.length > 0 && this.searchResponse.length != undefined) {
          console.log("ok");
          this.listdata = true;
        } else {
          console.log("not");
          this.listdata = false;
        }
      }, 2000)
    });
    // this.loader = false;
  }

  filterValue() {
    this.selectedGender.forEach(element => {
      this.gender.push(element["itemName"])
      console.log(this.gender);
    })
    this.selectedAge.forEach(element => {
      this.age.push(element["itemName"])
    })
    this.selectedProductsBuy.forEach(element => {
      this.productBuy.push(element["itemName"])
    })
    this.selectedColos.forEach(element => {
      this.color.push(element["itemName"])
    })
    this.selectedBuyingStyle.forEach(element => {
      this.buyingStyle.push(element["itemName"])
    })
    this.selectedcompatibility.forEach(element => {
      this.compatibility.push(element["itemName"])
    })
    this.selectedEmail.forEach(element => {
      this.email.push(element["itemName"])
    })
    this.selectedWorkingStyle.forEach(element => {
      this.workingStyle.push(element["itemName"])
    })
    this.selectedPNB.forEach(element => {
      this.productNotBuying.push(element["itemName"])
    })
    this.selectedEmploymentAffinity.forEach(element => {
      this.EmploymentAffinity.push(element["itemName"])
    })
    this.selectedEmploymentNonAffinity.forEach(element => {
      this.EmploymentNonAffinity.push(element["itemName"])
    })
    this.selectedDominiant.forEach(element => {
      this.Dominiant.push(element["itemName"])
    })
  }
